<template>
	<view class="map-container">
		<view class="header">
			<text class="title">Âú∞ÂõæÂØºËà™</text>
			<text class="subtitle">Êü•ÁúãÊóÖË°åË∑ØÁ∫øÂíåÁõÆÁöÑÂú∞</text>
		</view>
		
		<!-- Âú∞ÂõæÊéß‰ª∂ -->
		<view class="map-controls">
			<button class="control-btn" @click="locateMe">
				<text class="control-icon">üìç</text>
				<text class="control-text">ÂÆö‰Ωç</text>
			</button>
			<button class="control-btn" @click="searchLocation">
				<text class="control-icon">üîç</text>
				<text class="control-text">ÊêúÁ¥¢</text>
			</button>
			<button class="control-btn" @click="showRoute">
				<text class="control-icon">üõ£Ô∏è</text>
				<text class="control-text">Ë∑ØÁ∫ø</text>
			</button>
		</view>
		
		<!-- ËÖæËÆØÂú∞ÂõæÁªÑ‰ª∂ -->
		<view class="map-content">
			<map 
				id="tencentMap"
				:longitude="mapCenter.longitude"
				:latitude="mapCenter.latitude"
				:scale="mapScale"
				:markers="markers"
				:polyline="polyline"
				:show-location="true"
				:enable-zoom="true"
				:enable-scroll="true"
				:enable-rotate="false"
				:enable-overlooking="false"
				:enable-satellite="false"
				:enable-traffic="false"
				class="tencent-map"
				@regionchange="onRegionChange"
				@markertap="onMarkerTap"
				@callouttap="onCalloutTap"
				@controltap="onControlTap"
				@tap="onMapTap"
			></map>
			
			<!-- Âú∞ÂõæÂä†ËΩΩÁä∂ÊÄÅ -->
			<view class="map-loading" v-if="isLoading">
				<text class="loading-text">Âú∞ÂõæÂä†ËΩΩ‰∏≠...</text>
			</view>
		</view>
		
		<!-- ‰ΩçÁΩÆ‰ø°ÊÅØÊòæÁ§∫ -->
		<view class="location-info" v-if="currentLocation.address">
			<text class="location-title">ÂΩìÂâç‰ΩçÁΩÆ</text>
			<text class="location-address">{{ currentLocation.address }}</text>
			<text class="location-coords">
				ÁªèÂ∫¶: {{ currentLocation.longitude.toFixed(6) }}, 
				Á∫¨Â∫¶: {{ currentLocation.latitude.toFixed(6) }}
			</text>
		</view>
		
		<view class="action-buttons">
			<button class="action-btn" @click="goBack">
				<text>ËøîÂõûÈ¶ñÈ°µ</text>
			</button>
		</view>
		
		<!-- ÊêúÁ¥¢Ê®°ÊÄÅÊ°Ü -->
		<view class="modal" v-if="showSearchModal">
			<view class="modal-content">
				<view class="modal-header">
					<text class="modal-title">ÊêúÁ¥¢Âú∞ÁÇπ</text>
					<button class="close-btn" @click="hideSearchModal">√ó</button>
				</view>
				<view class="search-input">
					<input 
						v-model="searchKeyword" 
						placeholder="ËæìÂÖ•Âú∞ÁÇπÂêçÁß∞ÊàñÂú∞ÂùÄ"
						class="search-field"
						@confirm="performSearch"
					/>
					<button class="search-btn" @click="performSearch">ÊêúÁ¥¢</button>
				</view>
				<view class="search-results">
					<view 
						v-for="(result, index) in searchResults" 
						:key="index"
						class="search-result"
						@click="selectSearchResult(result)"
					>
						<text class="result-name">{{ result.title }}</text>
						<text class="result-address">{{ result.address }}</text>
					</view>
				</view>
			</view>
		</view>
	</view>
</template>

<script>
	import { mapConfig, mapUtils } from '@/config/map.config.js'
	
	export default {
		data() {
			return {
				// Âú∞Âõæ‰∏≠ÂøÉÁÇπ
				mapCenter: {
					longitude: mapConfig.defaultCenter.longitude,
					latitude: mapConfig.defaultCenter.latitude
				},
				// Âú∞ÂõæÁº©ÊîæÁ∫ßÂà´
				mapScale: mapConfig.zoomLevel,
				// Ê†áËÆ∞ÁÇπ
				markers: [],
				// Ë∑ØÁ∫ø
				polyline: [],
				// ÂΩìÂâç‰ΩçÁΩÆ‰ø°ÊÅØ
				currentLocation: {
					longitude: 0,
					latitude: 0,
					address: ''
				},
				// Âä†ËΩΩÁä∂ÊÄÅ
				isLoading: false,
				// ÊêúÁ¥¢Áõ∏ÂÖ≥
				showSearchModal: false,
				searchKeyword: '',
				searchResults: []
			}
		},
		
		mounted() {
			this.initMap()
		},
		
		methods: {
			// ÂàùÂßãÂåñÂú∞Âõæ
			async initMap() {
				this.isLoading = true
				try {
					// Ëé∑ÂèñÂΩìÂâç‰ΩçÁΩÆ
					const location = await mapUtils.getCurrentLocation()
					this.mapCenter.longitude = location.longitude
					this.mapCenter.latitude = location.latitude
					
					// Ëé∑ÂèñÂΩìÂâç‰ΩçÁΩÆÂú∞ÂùÄ
					const addressInfo = await mapUtils.reverseGeocode(location.latitude, location.longitude)
					this.currentLocation = {
						longitude: location.longitude,
						latitude: location.latitude,
						address: addressInfo.address
					}
					
					// Ê∑ªÂä†ÂΩìÂâç‰ΩçÁΩÆÊ†áËÆ∞
					this.addMarker({
						id: 0,
						longitude: location.longitude,
						latitude: location.latitude,
						title: 'ÂΩìÂâç‰ΩçÁΩÆ',
						iconPath: '/static/images/map/current-location.png',
						width: 40,
						height: 40
					})
					
					uni.showToast({
						title: 'Âú∞ÂõæÂàùÂßãÂåñÊàêÂäü',
						icon: 'success'
					})
				} catch (error) {
					console.error('Âú∞ÂõæÂàùÂßãÂåñÂ§±Ë¥•:', error)
					uni.showToast({
						title: 'Âú∞ÂõæÂàùÂßãÂåñÂ§±Ë¥•Ôºå‰ΩøÁî®ÈªòËÆ§‰ΩçÁΩÆ',
						icon: 'none'
					})
				} finally {
					this.isLoading = false
				}
			},
			
			// ÂÆö‰ΩçÂà∞ÂΩìÂâç‰ΩçÁΩÆ
			async locateMe() {
				try {
					const location = await mapUtils.getCurrentLocation()
					this.mapCenter.longitude = location.longitude
					this.mapCenter.latitude = location.latitude
					this.mapScale = 16 // ÊîæÂ§ßÂà∞Êõ¥ËØ¶ÁªÜÁöÑÁ∫ßÂà´
					
					uni.showToast({
						title: 'Â∑≤ÂÆö‰ΩçÂà∞ÂΩìÂâç‰ΩçÁΩÆ',
						icon: 'success'
					})
				} catch (error) {
					console.error('ÂÆö‰ΩçÂ§±Ë¥•:', error)
					uni.showToast({
						title: 'ÂÆö‰ΩçÂ§±Ë¥•ÔºåËØ∑Ê£ÄÊü•ÊùÉÈôêËÆæÁΩÆ',
						icon: 'none'
					})
				}
			},
			
			// ÊêúÁ¥¢Âú∞ÁÇπ
			searchLocation() {
				this.showSearchModal = true
			},
			
			// ÊâßË°åÊêúÁ¥¢
			async performSearch() {
				if (!this.searchKeyword.trim()) {
					uni.showToast({
						title: 'ËØ∑ËæìÂÖ•ÊêúÁ¥¢ÂÖ≥ÈîÆËØç',
						icon: 'none'
					})
					return
				}
				
				this.isLoading = true
				try {
					const results = await mapUtils.searchPlaces(this.searchKeyword)
					this.searchResults = results.map((place, index) => ({
						id: index,
						title: place.title,
						address: place.address,
						longitude: place.location.lng,
						latitude: place.location.lat
					}))
				} catch (error) {
					console.error('ÊêúÁ¥¢Â§±Ë¥•:', error)
					uni.showToast({
						title: 'ÊêúÁ¥¢Â§±Ë¥•ÔºåËØ∑Á®çÂêéÈáçËØï',
						icon: 'none'
					})
				} finally {
					this.isLoading = false
				}
			},
			
			// ÈÄâÊã©ÊêúÁ¥¢ÁªìÊûú
			selectSearchResult(result) {
				// ÁßªÂä®Âú∞ÂõæÂà∞ÊêúÁ¥¢ÁªìÊûú‰ΩçÁΩÆ
				this.mapCenter.longitude = result.longitude
				this.mapCenter.latitude = result.latitude
				this.mapScale = 16
				
				// Ê∑ªÂä†Ê†áËÆ∞
				this.addMarker({
					id: Date.now(),
					longitude: result.longitude,
					latitude: result.latitude,
					title: result.title,
					iconPath: '/static/images/map/marker.png',
					width: 30,
					height: 30,
					callout: {
						content: result.title,
						color: '#ffffff',
						bgColor: '#1A9E8F',
						display: 'ALWAYS'
					}
				})
				
				this.hideSearchModal()
				uni.showToast({
					title: `Â∑≤ÂÆö‰ΩçÂà∞${result.title}`,
					icon: 'success'
				})
			},
			
			// ÊòæÁ§∫Ë∑ØÁ∫øËßÑÂàí
			showRoute() {
				uni.showToast({
					title: 'Ë∑ØÁ∫øËßÑÂàíÂäüËÉΩÂºÄÂèë‰∏≠',
					icon: 'none'
				})
			},
			
			// Ê∑ªÂä†Ê†áËÆ∞ÁÇπ
			addMarker(marker) {
				this.markers.push(marker)
			},
			
			// Ê∏ÖÈô§ÊâÄÊúâÊ†áËÆ∞
			clearMarkers() {
				this.markers = this.markers.filter(marker => marker.id === 0) // ‰øùÁïôÂΩìÂâç‰ΩçÁΩÆÊ†áËÆ∞
			},
			
			// Âú∞ÂõæÂå∫ÂüüÂèòÂåñ‰∫ã‰ª∂
			onRegionChange(e) {
				// ÂèØ‰ª•Âú®ËøôÈáåÂ§ÑÁêÜÂú∞ÂõæÊãñÂä®ÊàñÁº©Êîæ‰∫ã‰ª∂
				if (e.type === 'end') {
					// Âú∞ÂõæÊãñÂä®ÁªìÊùüÔºåÂèØ‰ª•Ëé∑ÂèñÊñ∞ÁöÑ‰∏≠ÂøÉÁÇπÂùêÊ†á
				}
			},
			
			// Ê†áËÆ∞ÁÇπÁÇπÂáª‰∫ã‰ª∂
			onMarkerTap(e) {
				const markerId = e.markerId
				const marker = this.markers.find(m => m.id === markerId)
				if (marker) {
					uni.showModal({
						title: marker.title,
						content: 'ÊÇ®ÊÉ≥ÂØπËøô‰∏™Âú∞ÁÇπÂÅö‰ªÄ‰πàÔºü',
						showCancel: true,
						cancelText: 'ÂèñÊ∂à',
						confirmText: 'ËÆæ‰∏∫ÁõÆÁöÑÂú∞',
						success: (res) => {
							if (res.confirm) {
								uni.showToast({
									title: 'Â∑≤ËÆæ‰∏∫ÁõÆÁöÑÂú∞',
									icon: 'success'
								})
							}
						}
					})
				}
			},
			
			// Âú∞ÂõæÁÇπÂáª‰∫ã‰ª∂
			onMapTap(e) {
				// ÂèØ‰ª•Âú®ËøôÈáåÂÆûÁé∞ÁÇπÂáªÂú∞ÂõæÊ∑ªÂä†Ê†áËÆ∞ÁöÑÂäüËÉΩ
				const { latitude, longitude } = e.detail
				uni.showModal({
					title: 'Ê∑ªÂä†Ê†áËÆ∞',
					content: 'ÊòØÂê¶Âú®Ê≠§‰ΩçÁΩÆÊ∑ªÂä†Ê†áËÆ∞Ôºü',
					showCancel: true,
					cancelText: 'ÂèñÊ∂à',
					confirmText: 'Ê∑ªÂä†',
					success: (res) => {
						if (res.confirm) {
							this.addMarker({
								id: Date.now(),
								longitude: longitude,
								latitude: latitude,
								title: 'Ëá™ÂÆö‰πâÊ†áËÆ∞',
								iconPath: '/static/images/map/marker.png',
								width: 30,
								height: 30
							})
							uni.showToast({
								title: 'Ê†áËÆ∞Ê∑ªÂä†ÊàêÂäü',
								icon: 'success'
							})
						}
					}
				})
			},
			
			// ÈöêËóèÊêúÁ¥¢Ê®°ÊÄÅÊ°Ü
			hideSearchModal() {
				this.showSearchModal = false
				this.searchKeyword = ''
				this.searchResults = []
			},
			
			goBack() {
				uni.navigateBack()
			}
		}
	}
</script>

<style>
	.map-container {
		min-height: 100vh;
		background-color: #f5f7fa;
		display: flex;
		flex-direction: column;
	}
	
	.header {
		text-align: center;
		padding: 30rpx 30rpx 20rpx;
		background: linear-gradient(135deg, #1A9E8F 0%, #2ECC71 100%);
		color: white;
	}
	
	.title {
		font-size: 36rpx;
		font-weight: bold;
		display: block;
		margin-bottom: 8rpx;
	}
	
	.subtitle {
		font-size: 24rpx;
		opacity: 0.9;
		display: block;
	}
	
	/* Âú∞ÂõæÊéß‰ª∂Ê†∑Âºè */
	.map-controls {
		display: flex;
		justify-content: space-around;
		padding: 20rpx 30rpx;
		background-color: white;
		border-bottom: 1rpx solid #e5e5e5;
	}
	
	.control-btn {
		display: flex;
		flex-direction: column;
		align-items: center;
		background: none;
		border: none;
		padding: 15rpx 25rpx;
		border-radius: 15rpx;
		transition: all 0.3s;
	}
	
	.control-btn:active {
		background-color: #f8f9fa;
		transform: scale(0.95);
	}
	
	.control-icon {
		font-size: 36rpx;
		margin-bottom: 8rpx;
	}
	
	.control-text {
		font-size: 22rpx;
		color: #666;
	}
	
	/* Âú∞ÂõæÂÜÖÂÆπÂå∫Âüü */
	.map-content {
		flex: 1;
		position: relative;
	}
	
	.tencent-map {
		width: 100%;
		height: 100%;
	}
	
	/* Âú∞ÂõæÂä†ËΩΩÁä∂ÊÄÅ */
	.map-loading {
		position: absolute;
		top: 50%;
		left: 50%;
		transform: translate(-50%, -50%);
		background-color: rgba(255, 255, 255, 0.9);
		padding: 30rpx 50rpx;
		border-radius: 20rpx;
		box-shadow: 0 4rpx 20rpx rgba(0, 0, 0, 0.1);
		z-index: 10;
	}
	
	.loading-text {
		font-size: 28rpx;
		color: #666;
	}
	
	/* ‰ΩçÁΩÆ‰ø°ÊÅØÊòæÁ§∫ */
	.location-info {
		background-color: white;
		padding: 25rpx 30rpx;
		border-top: 1rpx solid #e5e5e5;
	}
	
	.location-title {
		font-size: 26rpx;
		font-weight: bold;
		color: #333;
		display: block;
		margin-bottom: 10rpx;
	}
	
	.location-address {
		font-size: 24rpx;
		color: #666;
		display: block;
		margin-bottom: 8rpx;
	}
	
	.location-coords {
		font-size: 20rpx;
		color: #999;
		display: block;
	}
	
	/* Êìç‰ΩúÊåâÈíÆ */
	.action-buttons {
		padding: 20rpx 30rpx;
		background-color: white;
		border-top: 1rpx solid #e5e5e5;
	}
	
	.action-btn {
		background-color: #1A9E8F;
		color: white;
		border-radius: 10rpx;
		padding: 25rpx;
		font-size: 28rpx;
		font-weight: bold;
		width: 100%;
	}
	
	/* ÊêúÁ¥¢Ê®°ÊÄÅÊ°ÜÊ†∑Âºè */
	.modal {
		position: fixed;
		top: 0;
		left: 0;
		right: 0;
		bottom: 0;
		background-color: rgba(0, 0, 0, 0.5);
		display: flex;
		align-items: center;
		justify-content: center;
		z-index: 1000;
	}
	
	.modal-content {
		background-color: white;
		border-radius: 20rpx;
		width: 90%;
		max-width: 600rpx;
		max-height: 80%;
		overflow: hidden;
	}
	
	.modal-header {
		display: flex;
		justify-content: space-between;
		align-items: center;
		padding: 30rpx;
		border-bottom: 1rpx solid #eee;
	}
	
	.modal-title {
		font-size: 32rpx;
		font-weight: bold;
		color: #333;
	}
	
	.close-btn {
		background: none;
		border: none;
		font-size: 40rpx;
		color: #999;
		padding: 0;
		width: 60rpx;
		height: 60rpx;
		display: flex;
		align-items: center;
		justify-content: center;
	}
	
	.search-input {
		padding: 30rpx;
		display: flex;
		gap: 15rpx;
	}
	
	.search-field {
		flex: 1;
		border: 1rpx solid #ddd;
		border-radius: 10rpx;
		padding: 20rpx;
		font-size: 26rpx;
	}
	
	.search-btn {
		background-color: #1A9E8F;
		color: white;
		border-radius: 10rpx;
		padding: 20rpx 30rpx;
		font-size: 26rpx;
	}
	
	.search-results {
		max-height: 400rpx;
		overflow-y: auto;
		padding: 0 30rpx 30rpx;
	}
	
	.search-result {
		padding: 20rpx;
		border-bottom: 1rpx solid #eee;
		transition: background-color 0.3s;
	}
	
	.search-result:last-child {
		border-bottom: none;
	}
	
	.search-result:active {
		background-color: #f8f9fa;
	}
	
	.result-name {
		font-size: 26rpx;
		font-weight: bold;
		color: #333;
		display: block;
		margin-bottom: 5rpx;
	}
	
	.result-address {
		font-size: 22rpx;
		color: #666;
	}
</style>